@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700,800&display=swap&subset=cyrillic);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.todo-item{position:relative;align-items:center;background-color:white;display:grid;grid-template-columns:1fr minmax(100px, 4fr) 1fr;margin:1rem auto;width:90vw;border-radius:.5rem;min-width:290px;max-width:800px;transition:.3s;padding:1rem 0;overflow:hidden;text-align:left}@media all and (max-width: 492px){.todo-item{padding:0}}.todo-item__check{position:relative;width:100%;display:flex;align-content:center;justify-content:center;height:100%}.todo-item span{transition:.3s;word-wrap:break-word;display:block}.todo-item .buttons{justify-self:end;align-self:center;display:flex;flex-direction:row;align-content:center;padding-right:.5rem;text-align:center}.todo-item--done .complete-indicator{left:0}.todo-item input{margin:0 1rem}.todo-subitem{color:white;width:80vw;margin:0 auto;display:grid;grid-template-columns:2.5rem minmax(150px, 680px) 2.5rem;min-width:290px;max-width:700px;position:relative;border-bottom:1px solid white;word-wrap:break-word}.todo-subitem__check{position:relative;width:100%;height:100%;display:flex;align-content:center;justify-content:center}.todo-subitem__text{margin:0;font-weight:400;display:flex;flex-direction:column;align-content:center;justify-content:center;position:relative;word-wrap:break-word}.todo-subitem input{margin:0 1rem;align-self:center;height:1.5rem;outline:none;background:none;border:none;color:white}:root{box-sizing:border-box;font-size:1rem}@media all and (max-width: 414px){:root{font-size:.9rem}}*{margin:0;padding:0;box-sizing:inherit;font-family:'Raleway', sans-serif}body{overflow-x:hidden}input{font-family:'Open Sans', sans-serif}input::-webkit-input-placeholder{color:rgba(255,255,255,0.35)}input:-ms-input-placeholder{color:rgba(255,255,255,0.35)}input::-ms-input-placeholder{color:rgba(255,255,255,0.35)}input::placeholder{color:rgba(255,255,255,0.35)}.button{margin:.5rem;cursor:pointer;outline:none}.button--filter{background-color:#9593ff;color:white;font-weight:bold;border:none;padding:.5rem 1.5rem;border-radius:.5rem;margin-top:1rem;transition:.3s}.button--filter:active{transform:scale(0.9)}.button--filter.active{background-color:indigo}input[type='checkbox']{cursor:pointer}.date{position:absolute;right:1rem;padding-bottom:.27rem;bottom:0;font-size:.75rem;color:gray;text-align:center}@media all and (max-width: 492px){.date{left:50%;transform:translateX(-50%)}}.complete-indicator{position:absolute;width:100%;height:4px;background-color:dodgerblue;bottom:0;left:-100%;transition:.6s ease-in-out}#dark-check{opacity:0;position:absolute}#dark-check:checked ~ .dark-mode>.toggle{left:100%;transform:translateX(-108%)}.dark-mode{display:inline-block;position:relative;height:2rem;width:3.5rem;border-radius:1rem;box-shadow:0 0 5px gray}.dark-mode .toggle{position:absolute;height:1.7rem;width:1.7rem;background:#61dafb no-repeat center;background-size:70%;border-radius:.85rem;left:3px;top:2.5px;transition:.3s}.dark-mode .toggle:hover{cursor:pointer}.wrapper{background-color:black;height:100vh;width:100vw;text-align:center;font-family:'Open Sans', sans-serif;overflow-x:hidden;color:#282c34;transition:.2s}.wrapper--light{-webkit-filter:invert(1);filter:invert(1)}.wrapper .container{width:90%;margin:0 auto;padding:1rem 0}.wrapper .container header{position:relative;display:flex;flex-direction:row;justify-content:space-between}.wrapper .container h3{font-family:'Dancing Script', cursive;font-size:3rem;color:white;line-height:1;text-align:left}.wrapper .container .main-input{color:white}.wrapper .container .main-input{outline:none;border:none;border-bottom:1px solid white;padding:0 0 .5rem;width:40vw;background-color:transparent;min-width:280px;max-width:700px}.wrapper .container .isDone{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);pointer-events:none}@media all and (max-width: 375px){.wrapper .container .isDone{left:1rem}}.wrapper .container .isDone--todo{left:2rem;padding:0}.wrapper .container .isDoneCheckbox{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);margin:0;height:1.5rem;width:1.5rem;opacity:0;z-index:2;padding-left:1rem;align-self:center}.wrapper .container .isDoneCheckbox--todo{left:2rem;padding:0}.wrapper .container .checkSpan{height:1rem;width:1rem;padding:0;background-color:transparent;border:1px solid gray;position:absolute;border-radius:.25rem;left:50%;top:50%;transform:translate(-50%, -50%);pointer-events:none;z-index:1}.wrapper .container .checkSpan--todo{left:2rem;padding:0}

